import React, { Component } from 'react'
import { addgoodscate, editgoodscate, goodscatelist } from '../../api/goods'
import getUrlParam from '../../tool/getUrlParam'
import { Form, Input, Button, message, Select, Icon, Upload } from 'antd'
const Option = Select.Option
const { TextArea } = Input
const Dragger = Upload.Dragger
class WrappedRegistrationForm extends Component {
	constructor(props) {
		super(props)
		this.state = {
			confirmDirty: false,
			goodscatetype: [],
			editimgsrc: '',
			radiovalue: null,
			distextarea: true // 
		}
	}

	handleSubmit = (e) => {
		e.preventDefault()
		this.props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
				if (getUrlParam('id')) {
					let arr = { id: getUrlParam('id') }
					editgoodscate(Object.assign(values, arr))
						.then((res) => {
							console.log(res)
							message.success(res.message)
						})
						.catch((err) => {
							message.error(err.message)
							console.log(err)
						})
				} else {
					addgoodscate(values)
						.then((res) => {
							if (res.status === 1) {
								message.success(res.message)
							} else {
								message.error(res.message)
							}
						})
						.catch((err) => {
							console.log(err)
							message.error(err.message)
						})
				}
			}
		})
	}
	// 
	goodscatelist(arr) {
		goodscatelist(arr)
			.then((res) => {
				if (arr.pid === 0) {
					this.setState({
						goodscatetype: res.data
					})
				} else {
					const {
						cate_img,
						description,
						filter_attr,
						keywords,
						link,
						pid,
						sub_title,
						template,
						title,
						sort
					} = res.data[0]
					this.props.form.setFieldsValue({
						description,
						filter_attr,
						keywords,
						link,
						pid,
						sub_title,
						template,
						title,
						sort
					})
					this.setState({
						editimgsrc: cate_img
					})
				}
			})
			.catch((err) => {
				console.log('err: ', err)
			})
	}

	renderOptions = () => {
		return this.state.goodscatetype.map((element) => (
			<Option key={element._id} value={element._id}>
				{element.title}
			</Option>
		))
	}
	componentDidMount() {
		this.goodscatelist({
			pid: 0
		})
		if (getUrlParam('id')) {
			this.setState({
				title: ''
			})
			this.goodscatelist({
				_id: getUrlParam('id')
			})
		} else {
			this.setState({
				title: ''
			})
		}
	}
	render() {
		const { getFieldDecorator } = this.props.form
		const formItemLayout = {
			labelCol: {
				xs: { span: 24 },
				sm: { span: 8 }
			},
			wrapperCol: {
				xs: { span: 24 },
				sm: { span: 16 }
			}
		}
		const tailFormItemLayout = {
			wrapperCol: {
				xs: {
					span: 24,
					offset: 0
				},
				sm: {
					span: 16,
					offset: 8
				}
			}
		}
		let editimg = ''
		let editrequired = ''
		if (this.state.editimgsrc !== '') {
			editimg = (
				<Form.Item label="">
					<img
						alt=""
						style={{ maxWidth: '10vw', maxHeight: '10vw' }}
						src={`http://127.0.0.1:7001${this.state.editimgsrc}`}
					/>
				</Form.Item>
			)
			editrequired = false
		} else {
			editrequired = true
		}
		const props = {
			name: 'file',
			action: '/admin/uploadimg',
			onChange(info) {
				const status = info.file.status
				if (status !== 'uploading') {
					console.log(info.file, info.fileList)
				}
				if (status === 'done') {
					message.success(`${info.file.name} file uploaded successfully.`)
				} else if (status === 'error') {
					message.error(`${info.file.name} file upload failed.`)
				}
			}
		}
		return (
			<Form {...formItemLayout} onSubmit={this.handleSubmit}>
				<h1>{this.state.title}</h1>
				<Form.Item label="">
					{getFieldDecorator('title', {
						rules: [
							{
								required: true,
								message: 'Please input your title!'
							}
						]
					})(<Input placeholder="" />)}
				</Form.Item>
				<Form.Item label="">
					{getFieldDecorator('pid', {
						rules: [
							{
								required: true,
								message: 'Please input your title!'
							}
						]
					})(
						<Select>
							<Option key={0} value={0}>
								
							</Option>
							{this.renderOptions()}
						</Select>
					)}
				</Form.Item>
				{editimg}

				<Form.Item label="">
					{getFieldDecorator('cate_img', {
						rules: [
							{
								required: editrequired,
								message: 'Please input your description!'
							}
						]
					})(
						<Dragger {...props}>
							<p className="ant-upload-drag-icon">
								<Icon type="inbox" />
							</p>
							<p className="ant-upload-text">Click or drag file to this area to upload</p>
							<p className="ant-upload-hint">
								Support for a single or bulk upload. Strictly prohibit from uploading company data or
								other band files
							</p>
						</Dragger>
					)}
				</Form.Item>
				<Form.Item label="">
					{getFieldDecorator('filter_attr', {
						rules: [
							{
								required: true,
								message: 'Please input your title!'
							}
						]
					})(
						<Select>
							<Option key={0} value={0}>
								
							</Option>
							{this.renderOptions()}
						</Select>
					)}
				</Form.Item>
				<Form.Item label="">
					{getFieldDecorator('link', {
						rules: [
							{
								required: true,
								message: 'Please input your description!'
							}
						]
					})(<Input placeholder="" />)}
				</Form.Item>
				<Form.Item label="">
					{getFieldDecorator('template', {
						rules: [
							{
								required: true,
								message: 'Please input your description!'
							}
						]
					})(<Input placeholder="" />)}
				</Form.Item>
				<Form.Item label="seo">
					{getFieldDecorator('sub_title', {
						rules: [
							{
								required: true,
								message: 'Please input your description!'
							}
						]
					})(<TextArea placeholder="" />)}
				</Form.Item>
				<Form.Item label="seo">
					{getFieldDecorator('description', {
						rules: [
							{
								required: true,
								message: 'Please input your description!'
							}
						]
					})(<TextArea placeholder="" />)}
				</Form.Item>
				<Form.Item label="seo">
					{getFieldDecorator('keywords', {
						rules: [
							{
								required: true,
								message: 'Please input your description!'
							}
						]
					})(<TextArea placeholder="" />)}
				</Form.Item>
				<Form.Item label=" ">
					{getFieldDecorator('sort', {
						rules: [
							{
								required: true,
								message: 'Please input your description!'
							}
						]
					})(<Input placeholder="" />)}
				</Form.Item>
				<Form.Item {...tailFormItemLayout}>
					<Button type="primary" htmlType="submit">
						
					</Button>
				</Form.Item>
			</Form>
		)
	}
}
const goodscate = Form.create({ name: 'register' })(WrappedRegistrationForm)
export default goodscate
